<template>
  <v-card>
    <v-card-title>
      <T> /  </T>
    </v-card-title>

    <v-skeleton-loader v-if="loading" type="list-item-three-line"/>

    <v-form v-else v-model="formIsValid" @submit.prevent="$emit('onSend')">
      <v-card-text>
        <ExpansionPanels v-model="openedPanels">
          <Panel :header="$t('   ')">
            <v-col cols="12">
              <SwitchField
                  :label="$t('')" :hint="$t(' ')" v-model="model.when.now"
              />
            </v-col>

            <v-col cols="12" md="6">
              <v-menu v-model="dateMenu" min-width="auto" :close-on-content-click="false">
                <template v-slot:activator="{ on }">
                  <TextField
                      :label="$t('')" :hint="$t(' ')"
                      :disabled="model.when.now" :readonly="true"
                      :value="dateFormatted" v-on="on"
                      :rules="model.when.now ? [] : [value => !!value || $t('  ')]"
                  />
                </template>
                <v-date-picker
                    v-model="model.when.date" :locale="$store.getters.locale_metadata.Language"
                    :first-day-of-week="1" :min="new Date().toISOString()"
                >
                  <template v-slot:default>
                    <v-spacer/>
                    <v-btn color="primary" @click="dateMenu = false">
                      <T></T>
                    </v-btn>
                  </template>
                </v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="6">
              <v-menu v-model="timeMenu" min-width="auto" :close-on-content-click="false">
                <template v-slot:activator="{ on }">
                  <TextField
                      :label="$t('')" :hint="$t(' ')"
                      :disabled="model.when.now" :readonly="true"
                      v-model="model.when.time" v-on="on"
                      :rules="model.when.now ? [] : [value => !!value || $t('  ')]"
                  />
                </template>
                <v-time-picker v-model="model.when.time" format="24hr">
                  <template v-slot:default>
                    <v-spacer/>
                    <v-btn color="primary" @click="timeMenu = false">
                      <T></T>
                    </v-btn>
                  </template>
                </v-time-picker>
              </v-menu>
            </v-col>
          </Panel>

          <Panel :header="$t(' ')">
            <v-col cols="12" md="4">
              <SwitchField
                  :label="$t('  ')"
                  :hint="$t('    ')"
                  v-model="model.filters.all_groups"
              />
            </v-col>
            <v-col cols="12" md="8">
              <SelectField
                  :label="$t(' ')" :hint="$t('   ')" :multiple="true"
                  v-show="!model.filters.all_groups" :items="accountGroupsList"
                  itemValue="id" itemText="name"
                  v-model="model.filters.groups"
                  :rules="model.filters.all_groups ? [] : [value => !!value || $t('  ')]"
              />
            </v-col>

            <v-col cols="12" md="4">
              <SwitchField
                  :label="$t('   ')"
                  :hint="$t('  ,  ')"
                  v-model="model.filters.all_sexes"
              />
            </v-col>
            <v-col cols="12" md="8">
              <SelectField
                  :label="$t('')" :hint="$t('   ')" :items="sex"
                  v-show="!model.filters.all_sexes" v-model="model.filters.sex"
                  :rules="model.filters.all_sexes ? [] : [value => !!value || value === 0 || $t('  ')]"
              />
            </v-col>

            <v-col cols="12" md="4">
              <SwitchField
                  :label="$t('   ')" :hint="$t('   ')"
                  v-model="model.filters.all_ages"
              />
            </v-col>
            <v-col cols="12" md="4">
              <TextField
                  :label="$t(' ()')" :hint="$t('     ')"
                  v-show="!model.filters.all_ages" v-model="model.filters.age_range.from_age"
                  :rules="model.filters.all_ages ? [] : [value => !!value || $t('  ')]"
              />
            </v-col>
            <v-col cols="12" md="4">
              <TextField
                  :label="$t(' ()')" :hint="$t('     ')"
                  v-show="!model.filters.all_ages" v-model="model.filters.age_range.to_age"
                  :rules="model.filters.all_ages ? [] : [value => !!value || $t('  ')]"
              />
            </v-col>
          </Panel>

          <Panel :header="$t('  ')">
            <v-col cols="12">
              <Editor v-model="model.message.text" class="pb-3"/>
            </v-col>

            <v-col cols="12">
              <v-file-input
                  dense outlined filled :label="$t('')" prepend-icon="mdi-image-area"
                  :value="image" @change="e => $emit('changeImage', e)"
              />
            </v-col>
          </Panel>
        </ExpansionPanels>
      </v-card-text>

      <v-card-actions>
        <v-spacer/>
        <v-btn type="submit" color="primary" :disabled="!formIsValid || !(model.message.text || image)">
          <T class="mr-1"></T>
          {{ buttonDate }}
        </v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>

<script>
import ExpansionPanels from "@/components/ExpansionPanels";
import Panel from "@/components/ExpansionPanels/Panel.vue";
import TextField from "@/components/ExpansionPanels/TextField.vue";
import SwitchField from "@/components/ExpansionPanels/SwitchField.vue";
import SelectField from "@/components/ExpansionPanels/SelectField.vue";
import Editor from "@/components/ExpansionPanels/Editor.vue";

export default {
  components: {
    ExpansionPanels, Panel, TextField, SwitchField, SelectField, Editor
  },

  props: ["loading", "value", "accountGroupsList", "image"],

  emits: ["onSend", "input", "changeImage"],

  data: () => ({
    openedPanels: [0, 1, 2],
    formIsValid: undefined,
    dateMenu: false,
    timeMenu: false
  }),

  methods: {
    formatDate(date) {
      if (!date) {
        return null;
      }
      const [year, month, day] = date.split("-")
      return `${day}.${month}.${year}`
    }
  },

  computed: {
    model: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit('input', v);
      }
    },
    sex() {
      return [
        {value: 1, text: this.$t("")},
        {value: 0, text: this.$t("")}
      ]
    },
    dateFormatted() {
      return this.formatDate(this.model.when.date)
    },
    buttonDate() {
      if (this.model.when.now) {
        return this.$t("");
      } else if (this.model.when.date && this.model.when.time) {
        return `${this.dateFormatted} ${this.$t('')} ${this.model.when.time}`
      } else {
        return "";
      }
    }
  }
}
</script>