export default {
    state: {
        info: {}
    },

    getters: {
        info(state) {
            return state.info;
        }
    },

    mutations: {
        setInfo(state, info = {}) {
            state.info = info;
        }
    },

    actions: {
        async getInfo(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/info/"
                });
                context.commit("setInfo", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getOnlyAppInfo(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/info/app/"
                });
                context.commit("setInfo", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        }
    }
}