<template>
  <v-text-field
      dense outlined filled
      :label="label" :disabled="disabled" :readonly="readonly"
      :rules="rules" v-model="model" v-on="$listeners"
  >
    <template v-slot:prepend>
      <ToolTip :hint="hint" v-if="hint"/>

      <v-btn v-if="copy"
             icon
             @click="copyText"
      >
        <v-icon>mdi-content-copy</v-icon>
      </v-btn>
    </template>

    <template v-slot:label>
      <slot name="label"/>
    </template>
  </v-text-field>
</template>

<script>
import {ToolTip} from "carbis-ui-library";

export default {
  components: {
    ToolTip
  },

  computed: {
    model: {
      get() {
        return this.value
      },
      set(v) {
        this.$emit('input', v)
      }
    }
  },

  methods: {
    async copyText() {
      await navigator.clipboard.writeText(this.value);
    }
  },

  props: ["value", "label", "hint", "disabled", "readonly", "rules", "copy"]
}
</script>