<template>
  <v-app-bar app clipped-left color="primary" dark>
    <v-app-bar-nav-icon v-if="!hideUserInfo" class="d-flex d-lg-none" @click="$emit('showDrawer')"/>

    <div class="d-flex" v-if="$vuetify.breakpoint.smAndUp">
      <v-icon x-large>mdi-tag-heart-outline</v-icon>
      <h1 class="pl-3">ProBonus Bot</h1>
    </div>

    <div class="info-block d-flex flex-column justify-center" v-if="!infoLoading && $vuetify.breakpoint.smAndUp">
      <span>app: v{{ $store.getters.info.app_version }}</span>
      <span v-if="$store.getters.info.probonus_version">
        probonus: v{{ $store.getters.info.probonus_version }}
      </span>
    </div>

    <v-spacer/>

    <template v-if="!hideUserInfo">
      <v-alert dense text class="ma-0 mr-2">{{ $store.getters.authInfo.sub }}</v-alert>

      <v-btn icon @click="$emit('logout')">
        <v-icon large>mdi-logout</v-icon>
      </v-btn>
    </template>
  </v-app-bar>
</template>

<script>
export default {
  props: ["infoLoading", "hideUserInfo"],

  emits: ["showDrawer", "logout"]
}
</script>

<style>
.info-block {
  position: relative;
  margin-left: 1.3rem;
  font-size: .8em;
  opacity: .6;
  line-height: 1.6em;
  letter-spacing: 0;
  font-family: Consolas, 'Courier New', Courier, monospace;
}

.info-block::before {
  content: " ";
  height: 100%;
  width: 1px;
  position: absolute;
  left: -.5rem;
  background-color: currentColor;
  opacity: .6;
}
</style>