export default {
    state: {
        config: {},
        meta: {}
    },

    getters: {
        config(state) {
            return state.config;
        },
        meta(state) {
            return state.meta;
        }
    },

    mutations: {
        setConfig(state, config = {}) {
            state.config = config;
        },
        setMeta(state, meta = {}) {
            state.meta = meta;
        }
    },

    actions: {
        async getConfig(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/config/"
                });
                context.commit("setConfig", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getConfigMeta(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/config/meta/"
                });
                context.commit("setMeta", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async putConfig(context, config) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/config/",
                    method: "PUT",
                    body: config
                });
                context.commit("setConfig", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getProbonusConfigAddress(context) {
            try {
                return await context.dispatch("apiRequest", {
                    address: "/api/v1/config/probonus_settings/address/"
                });
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getProbonusInfo(context) {
            try {
                return await context.dispatch("apiRequest", {
                    address: "/api/v1/config/probonus-info/"
                });
            } catch (e) {
                context.commit("addError", e);
            }
        }
    }
}