export default {
    state: {
        reviews: []
    },

    getters: {
        reviews(state) {
            return state.reviews;
        }
    },

    mutations: {
        setReviews(state, reviews = []) {
            state.reviews = reviews;
        }
    },

    actions: {
        async getReviews(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/reviews/"
                });
                context.commit("setReviews", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getReview(context, id) {
            try {
                return await context.dispatch("apiRequest", {
                    address: `/api/v1/reviews/${id}/`
                });
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async setReviewProcessed(context, {id, body}) {
            try {
                return await context.dispatch("apiRequest", {
                    address: `/api/v1/reviews/${id}/`,
                    method: "PUT",
                    body: body
                });
            } catch (e) {
                context.commit("addError", e);
            }
        }
    }
}