<template>
  <ckeditor v-model="model" :config="config"/>
</template>

<script>
import CKEditor from "ckeditor4-vue";

export default {
  components: {
    ckeditor: CKEditor.component
  },

  props: ["value"],

  data: () => ({
    config: {
      extraPlugins: "emoji",
      emoji_followingSpace: true,
      toolbar: [
        {name: "insert", items: ["EmojiPanel"]},
        {name: "basicstyles", items: ["Bold", "Italic", "Strike", "Underline"]},
        {name: "links", items: ["Link"]}
      ],
      removeButtons: [],
      enterMode: 2,
      shiftEnterMode: 2,
      autoParagraph: false,
      removePlugins: "elementspath",
      versionCheck: false
    }
  }),

  computed: {
    model: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("input", v);
      }
    }
  }
}
</script>