<template>
  <v-switch
      dense inset persistent-hint
      :label="label" :disabled="disabled" class="ma-0" :rules="rules"
      v-model="model" :hint="secondHint"
  >
    <template v-slot:prepend>
      <ToolTip :hint="hint"/>
    </template>
  </v-switch>
</template>

<script>
import {ToolTip} from "carbis-ui-library";

export default {
  components: {
    ToolTip
  },

  computed: {
    model: {
      get() {
        return this.value
      },
      set(v) {
        this.$emit('input', v)
      }
    }
  },

  props: ["value", "label", "hint", "secondHint", "disabled", "rules"]
}
</script>
