<template>
  <v-data-table
      :search="search"
      :no-data-text="$t('  ')"
      :loading-text="$t('...')"
      :footer-props="{
      'page-text': '{0}-{1} ' + $t('') + ' {2}',
      'items-per-page-text': $t('-   '),
      'items-per-page-all-text': $t(''),
      'items-per-page-options': [5, 10, 20, 30, -1]
    }"
      v-bind="$attrs"
      v-on="$listeners"
  >
    <template v-slot:top>
      <v-row>
        <v-spacer/>
        <v-text-field
            append-icon="mdi-magnify"
            :label="$t('')"
            v-model="search"
            class="mr-5 mt-2"
            style="max-width: 300px"
        />
      </v-row>
    </template>

    <template v-for="(_, slot) of $scopedSlots" v-slot:[slot]="scope">
      <slot :name="slot" v-bind="scope"/>
    </template>
  </v-data-table>
</template>

<script>
export default {
  data: () => ({
    search: "",
  }),
}
</script>