<template>
  <fragment>
    <LoadingOverlay v-if="loading"/>

    <ReviewDetailInfo
        :loading=loading :review="review" :user="user" @processed="processed"
    />
  </fragment>
</template>

<script>
import {Fragment} from "vue-fragment";
import LoadingOverlay from "@/components/Loading/LoadingOverlay.vue";
import ReviewDetailInfo from "@/components/ReviewDetailInfo.vue";

export default {
  components: {
    ReviewDetailInfo, LoadingOverlay, Fragment
  },

  data: () => ({
    loading: true,
    review: {
      id: undefined,
      grade: undefined
    },
    user: undefined
  }),

  methods: {
    async processed() {
      this.loading = true;
      await this.$store.dispatch("setReviewProcessed", {id: this.review.id, body: {status: 2}});
      this.review = await this.$store.dispatch("getReview", this.$route.params.review_id);
      this.loading = false;
    }
  },

  async mounted() {
    document.title = this.$t(" ") + this.$route.params.review_id;
    this.review = await this.$store.dispatch("getReview", this.$route.params.review_id);
    if (this.review) {
      this.user = await this.$store.dispatch("getUserExtended", this.review.user_id);
    }
    this.loading = false;
  }
}
</script>