export default {
    state: {
        locale: {
            metadata: {},
            phrases: {}
        }
    },

    getters: {
        locale_phrases(state) {
            return state.locale.phrases;
        },
        locale_metadata(state) {
            return state.locale.metadata;
        },
        localePhrase(_, getters) {
            return (phrase) => {
                let phrases = getters.locale_phrases;
                let trans_phrase = phrases[phrase.replaceAll("\n", "\\n")];

                return trans_phrase !== undefined ? trans_phrase || phrase : process.env.NODE_ENV === "development" ? `->{ ${phrase} }<-`: phrase;
            }
        }
    },

    mutations: {
        setLocale(state, locale) {
            state.locale = locale;
        }
    },

    actions: {
        async getLocale(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/locale/"
                });
                context.commit("setLocale", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getLocalesList(context) {
            try {
                return await context.dispatch("apiRequest", {
                    address: "/api/v1/locale/list/"
                });
            } catch (e) {
                context.commit("addError", e);
            }
        }
    }
}