<template>
  <fragment>
    <LoadingOverlay v-if="loading"/>

    <MailingHistoryTable
        :loading="loading" :items="mailingWithDatetime" :deleting="deleting"
        @delete="(id) => deleteMailing(id)"
    />
  </fragment>
</template>

<script>
import {Fragment} from "vue-fragment";
import LoadingOverlay from "@/components/Loading/LoadingOverlay.vue";
import MailingHistoryTable from "@/components/MailingHistoryTable";

export default {
  components: {
    LoadingOverlay, Fragment, MailingHistoryTable
  },

  data: () => ({
    loading: true,
    deleting: false
  }),

  computed: {
    mailingWithDatetime() {
      return this.$store.getters.mailings.map(
          (mailing) => {
            mailing.formattedDatetime = (new Date(mailing.datetime)).toLocaleString();
            mailing.formattedText = [
                mailing.text, mailing.photo ? this.$t("[]") : undefined
            ].join(" ");
            return mailing;
          }
      )
    }
  },

  methods: {
    async deleteMailing(id) {
      this.deleting = true;
      await this.$store.dispatch("deleteUnsentMailing", id);
      this.deleting = false;

      this.loading = true;
      await this.$store.dispatch("getMailings");
      this.loading = false;
    }
  },

  async mounted() {
    document.title = this.$t(" ");
    await this.$store.dispatch("getMailings");
    this.loading = false;
  }
}
</script>
