<template>
  <v-card>
    <v-card-title>
      <T> /  </T>
    </v-card-title>

    <DataTable
        :items="items"
        item-key="id"
        :headers="headers"
        :sort-by="['datetime']"
        :sort-desc="[true]"
        :items-per-page="10"
        :loading="loading"
    >
      <template v-slot:item.formattedText="{ value }">
        <div style="max-width: 1px">
          {{ value }}
        </div>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon
            v-if="!(new Date(item.datetime) < Date.now())"
            color="black"
            @click="requestDeleteConfirm(item.id)"
        >
          mdi-trash-can
        </v-icon>
      </template>
    </DataTable>

    <ConfirmDeleteDialog
        :deleting="deleting" v-model="confirmDelete.show" :mailing_id="confirmDelete.id"
        @delete="(id) => $emit('delete', id)"
    />
  </v-card>
</template>

<script>
import ConfirmDeleteDialog from "./ConfirmDeleteDialog.vue";
import DataTable from "@/components/DataTable.vue";

export default {
  components: {
    ConfirmDeleteDialog, DataTable
  },

  props: ["loading", "items", "deleting"],

  emits: ["delete"],

  data: () => ({
    confirmDelete: {
      show: false,
      id: undefined
    }
  }),

  computed: {
    headers() {
      return [
        {
          text: "#",
          value: "id",
          width: 1,
          sortable: false,
          align: "center"
        },
        {
          text: this.$t("  "),
          value: "formattedDatetime",
          width: 1,
          sortable: false
        },
        {
          text: this.$t(""),
          value: "formattedText",
          sortable: false,
          cellClass: "text-truncate"
        },
        {
          text: this.$t(""),
          value: "counted_recipients",
          width: 1,
          sortable: false,
          align: "center"

        },
        {
          text: this.$t(""),
          value: "counted_received_recipients",
          width: 1,
          sortable: false,
          align: "center"
        },
        {
          text: this.$t(""),
          value: "actions",
          width: 1,
          sortable: false,
          align: "center"
        }
      ]
    }
  },

  methods: {
    requestDeleteConfirm(id) {
      this.confirmDelete.show = true;
      this.confirmDelete.id = id;
    }
  },

  watch: {
    items() {
      this.confirmDelete.show = false;
      this.confirmDelete.id = undefined;
    }
  }
}
</script>