import Vue from 'vue'
import Vuex from 'vuex'
import errorModule from "./errorModule";
import settingsModule from "./settingsModule";
import infoModule from "./infoModule";
import localModule from "./localeModule";
import authModule from "./authModule";
import botModule from "./botModule";
import mailingModule from "./mailingModule";
import reviewsModule from "./reviewsModule"
import usersModule from "./usersModule"
import reportsModule from "./reportsModule"

Vue.use(Vuex)

export default new Vuex.Store({
    actions: {
        async apiRequest(context, {address, method, body, multipart}) {
            let headers = {"Jwt": `${context.getters.tokens.tokenType} ${context.getters.tokens.accessToken}`}
            if (!multipart) {
                headers["Content-Type"] = "application/json"
            }
            try {
                let response = await fetch(address, {
                        method: method ? method : "GET",
                        body: body ? multipart ? body : JSON.stringify(body) : undefined,
                        headers: headers
                    }
                );
                let responseBody = await response.json();

                if (!response.ok) {
                    throw responseBody;
                }
                return responseBody;
            } catch (e) {
                if (!e.error_class) {
                    context.commit("setUnexpectedError", e);
                } else if (e.error_class === "AccessTokenExpiredError") {
                    await context.dispatch("updateRefreshToken");
                    return await context.dispatch("apiRequest", {
                        address: address,
                        method: method,
                        body: body,
                        multipart: multipart
                    });
                } else if (
                    e.error_class === "InvalidRefreshTokenError" ||
                    e.error_class === "NotAuthenticatedError" ||
                    (e.error_class === "AuthError" && e.detail === "Could not validate credentials")
                ) {
                    await context.dispatch("logout");
                } else {
                    throw e;
                }
            }
        }
    },

    modules: {
        errorModule, settingsModule, infoModule, localModule, authModule,
        botModule, mailingModule, reviewsModule, usersModule, reportsModule
    }
})
