<template>
  <fragment>
    <v-card>
      <v-card-title>
        <T> / UTM-</T>
      </v-card-title>

      <v-card-text>
        <v-row>
          <v-col cols="12" md="4">
            <v-menu v-model="dateMenu.from" min-width="auto" :close-on-content-click="false">
              <template v-slot:activator="{ on }">
                <TextField
                    :label="$t('')" :readonly="true"
                    :value="fromDateFormatted" v-on="on"
                    :rules="[value => !!value || $t('  ')]"
                />
              </template>
              <v-date-picker
                  v-model="model.fromDate" :locale="$store.getters.locale_metadata.Language"
                  :first-day-of-week="1" :max="new Date().toISOString()"
              >
                <template v-slot:default>
                  <v-spacer/>
                  <v-btn color="primary" @click="dateMenu.from = false">
                    <T></T>
                  </v-btn>
                </template>
              </v-date-picker>
            </v-menu>
          </v-col>

          <v-col cols="12" md="4">
            <v-menu v-model="dateMenu.to" min-width="auto" :close-on-content-click="false">
              <template v-slot:activator="{ on }">
                <TextField
                    :label="$t('')" :readonly="true"
                    :value="toDateFormatted" v-on="on"
                    :rules="[value => !!value || $t('  ')]"
                />
              </template>
              <v-date-picker
                  v-model="model.toDate" :locale="$store.getters.locale_metadata.Language"
                  :first-day-of-week="1" :max="new Date().toISOString()"
              >
                <template v-slot:default>
                  <v-spacer/>
                  <v-btn color="primary" @click="dateMenu.to = false">
                    <T></T>
                  </v-btn>
                </template>
              </v-date-picker>
            </v-menu>
          </v-col>

          <v-col cols="12" md="4">
            <v-btn color="primary" @click="$emit('makeReport')">
              <T> </T>
            </v-btn>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>

    <v-card class="mt-5">
      <DataTable
          :loading=loading :headers="headers" :items="items"
      >
        <template v-slot:body.append>
          <tr>
            <td class="text-right"><T></T></td>
            <td v-for="(sum, index) in sums" :key="index">{{ sum }}</td>
          </tr>
        </template>
      </DataTable>

      <div id="chart">
        <apexchart height="350" type="line" :series="series" :options="options"/>
      </div>
    </v-card>
  </fragment>
</template>

<script>
import DataTable from "@/components/DataTable.vue";
import TextField from "@/components/ExpansionPanels/TextField.vue";
import {Fragment} from "vue-fragment";

export default {
  components: {
    TextField, DataTable, Fragment
  },

  props: ["loading", "value", "reportData"],

  emits: ["input", "makeReport"],

  data: () => ({
    dateMenu: {
      from: false,
      to: false
    }
  }),

  computed: {
    model: {
      get() {
        return this.value
      },
      set(v) {
        this.$emit('input', v)
      }
    },
    fromDateFormatted() {
      return this.formatDate(this.model.fromDate);
    },
    toDateFormatted() {
      return this.formatDate(this.model.toDate);
    },
    headers() {
      if (!this.reportData.dates) {
        return undefined;
      }
      return [
        {
          text: this.$t("UTM-"),
          value: "utm"
        },
        ...this.reportData.dates.map(elem => {
          return {
            text: this.formatDate(elem),
            value: elem
          }
        }),
        {
          text: this.$t(""),
          value: "overall"
        }
      ];
    },
    items() {
      if (!this.reportData.rows) {
        return undefined;
      }
      return this.reportData.rows.map(elem => {
        let overall = 0;
        elem.count_by_dates.forEach(countByDate => {
          elem[countByDate.date] = countByDate.count;
          overall += countByDate.count;
        });
        elem.overall = overall;
        return elem;
      });
    },
    sums() {
      if (!this.reportData.dates) {
        return undefined;
      }
      let result = this.reportData.dates.map(elem => {
        let sum = 0;
        this.items.forEach(item => sum += item[elem]);
        return sum;
      });
      result.push(this.items.map(elem => elem.overall).reduce((a, b) => a + b, 0))
      return result;
    },
    series() {
      if (!this.reportData.rows) {
        return undefined;
      }
      return this.reportData.rows.map(elem => {
        return {
          name: elem.utm,
          data: elem.count_by_dates.map(dateCount => dateCount.count)
        };
      })
    },
    options() {
      return {
        chart: {
          type: 'line',
          zoom: {
            enabled: false
          }
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
          curve: 'straight'
        },
        grid: {
          row: {
            colors: ['#f3f3f3', 'transparent'],
            opacity: 0.5
          },
        },
        xaxis: {
          categories: this.reportData.dates?.map(elem => this.formatDate(elem)),
          title: {
            text: this.$t("")
          }
        },
        yaxis: {
          title: {
            text: this.$t("")
          }
        }
      }
    }
  },

  methods: {
    formatDate(date) {
      if (!date) {
        return null;
      }
      const [year, month, day] = date.split("-")
      return `${day}.${month}.${year}`
    }
  }
}
</script>
