<template>
  <v-select
      dense outlined filled :label="label" :disabled="disabled" :multiple="multiple"
      :rules="rules" :items="items" :item-text="itemText" :item-value="itemValue"
      :no-data-text="noDataText" v-model="model"
  >
    <template v-slot:prepend>
      <ToolTip :hint="hint"/>
    </template>
  </v-select>
</template>

<script>
import {ToolTip} from "carbis-ui-library";

export default {
  components: {
    ToolTip
  },

  computed: {
    model: {
      get() {
        return this.value
      },
      set(v) {
        this.$emit('input', v)
      }
    }
  },

  props: [
      "value", "items", "label", "hint", "multiple", "disabled", "rules", "itemText", "itemValue", "noDataText"
  ]
}
</script>