<template>
  <fragment>
    <LoadingOverlay v-if="loading"/>

    <UtmReportTableGraphics
        v-model="dates" @makeReport="makeReport" :reportData="reportData"
    />
  </fragment>
</template>

<script>
import {Fragment} from "vue-fragment";
import LoadingOverlay from "@/components/Loading/LoadingOverlay.vue";
import UtmReportTableGraphics from "@/components/UtmReportTableGraphics.vue";

export default {
  components: {
    Fragment, LoadingOverlay, UtmReportTableGraphics
  },

  data: () => ({
    loading: true,
    reportData: {
      dates: undefined,
      rows: undefined
    },
    dates: {
      fromDate: undefined,
      toDate: new Date().toISOString().slice(0, 10)
    }
  }),

  methods: {
    async makeReport() {
      this.loading = true;
      this.reportData = await this.$store.dispatch("getUtmReport", {
        dateFrom: this.dates.fromDate, dateTo: this.dates.toDate
      });
      this.loading = false;
    }
  },

  async mounted() {
    document.title = this.$t("");
    let currentDate = new Date();
    currentDate.setDate(currentDate.getDate() - 30);
    this.dates.fromDate = currentDate.toISOString().slice(0, 10);
    this.loading = false;
  }
}
</script>