<template>
  <v-card>
    <v-card-title>
      <T></T>
    </v-card-title>

    <DataTable
        :items="items"
        item-key="id"
        :headers="headers"
        :sort-by="['datetime_begin']"
        :sort-desc="[true]"
        :items-per-page="10"
        :loading="loading"
    >
      <template v-slot:item.formattedText="{ value }">
        <div style="max-width: 1px">
          {{ value }}
        </div>
      </template>

      <template v-slot:item.statusText="{ item }">
        {{ statuses[item.status] }}
      </template>

      <template v-slot:item.actions="{ item }">
        <router-link :to="{name: 'Review', params: {review_id: item.id}}">
          <v-icon color="black">
            mdi-eye
          </v-icon>
        </router-link>
      </template>
    </DataTable>
  </v-card>
</template>

<script>
import DataTable from "@/components/DataTable.vue";

export default {
  components: {
    DataTable
  },

  props: ["loading", "items"],

  emits: ["openReview"],

  computed: {
    headers() {
      return [
        {
          text: "#",
          value: "id",
          width: 1,
          sortable: false,
          align: "center"
        },
        {
          text: this.$t("  "),
          value: "formattedDatetimeBegin",
          width: 1,
          sortable: false
        },
        {
          text: this.$t(""),
          value: "grade",
          width: 1,
          sortable: false,
          align: "center"
        },
        {
          text: this.$t(""),
          value: "formattedText",
          sortable: false,
          cellClass: "text-truncate"
        },
        {
          text: this.$t(""),
          value: "statusText",
          sortable: false,
          width: 1,
          align: "center"
        },
        {
          text: this.$t(""),
          value: "actions",
          width: 1,
          sortable: false,
          align: "center"
        }
      ]
    },
    statuses() {
      return [
        this.$t(" "),
        this.$t(""),
        this.$t("")
      ]
    }
  }
}
</script>