<template>
    <div class="fill-height">
      <ErrorsPopup v-if="$store.getters.errors.length"/>

      <LoadingOverlay v-if="loading"/>

      <AppBar :infoLoading="loading" @showDrawer="drawer = true" @logout="$store.dispatch('logout')"/>
      <NavBar :infoLoading="loading" v-model="drawer"/>

      <v-main class="fill-height">
        <v-container fluid>
          <router-view/>
        </v-container>
      </v-main>
    </div>
</template>

<script>
import LoadingOverlay from "@/components/Loading/LoadingOverlay.vue";
import ErrorsPopup from "@/components/ErrorsPopup.vue";
import AppBar from "@/components/Main/AppBar.vue";
import NavBar from "@/components/Main/NavBar.vue";

export default {
  data: () => ({
    drawer: undefined,
    loading: true
  }),

  components: {
    LoadingOverlay, AppBar, NavBar, ErrorsPopup
  },

  async mounted() {
    await this.$store.dispatch("getInfo");
    this.loading = false;
  }
}
</script>