import Vue from 'vue'
import VueRouter from 'vue-router'
import Mailing from "@/views/Mailing.vue";
import MailingHistory from "@/views/MailingHistory.vue";
import Reviews from "@/views/Reviews.vue";
import ReviewDetail from "@/views/ReviewDetail.vue";
import UtmReport from "@/views/UtmReport.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    redirect: '/mailing/new/'
  },
  {
    path: '/settings/main/',
    name: 'MainSettings',
    component: () => import("@/views/MainSettings.vue")
  },
  {
    path: '/settings/phrases/',
    name: 'PhrasesSettings',
    component: () => import("@/views/PhrasesSettings.vue")
  },
  {
    path: '/settings/description-and-politics/',
    name: 'DescriptionsAndPoliticsSettings',
    component: () => import("@/views/DescriptionAndPoliticsSettings.vue")
  },
  {
    path: '/mailing/new/',
    name: 'Mailing',
    component: Mailing
  },
  {
    path: '/mailing/history/',
    name: 'MailingHistory',
    component: MailingHistory
  },
  {
    path: '/reviews/history/',
    name: 'ReviewsHistory',
    component: Reviews
  },
  {
    path: '/reviews/:review_id/',
    name: "Review",
    component: ReviewDetail
  },
  {
    path: '/report/',
    name: "Report",
    component: UtmReport
  }
  // {
  // path: '/mailing', // /mailing/new
  // component: BlankView,
  // children: [
  //   {
  //     path: '/new',
  //     component: Mailing
  //   },
  //   {
  //     path: ':mailing_id',
  //
  //   }
  // ]
  // },
  // {
  //   path: '/mailing/:mailing_id',
  //   name: 'MailingView',
  //   component: MailingHistory
  // },
]

const router = new VueRouter({
  routes
})

export default router
