<template>
  <fragment>
    <AppBar :infoLoading="loading" :hideUserInfo="true"/>

    <v-container fluid class="fill-height">
      <v-row align="center" justify="center">
        <Login/>
      </v-row>
    </v-container>
  </fragment>
</template>

<script>
import Login from "@/views/Login.vue";
import AppBar from "@/components/Main/AppBar.vue";
import {Fragment} from "vue-fragment";

export default {
  components: {
    Login, Fragment, AppBar
  },

  data: () => ({
    loading: true
  }),

  async mounted() {
    await this.$store.dispatch("getOnlyAppInfo");
    this.loading = false;
  }
}
</script>