import jwt_decode from "jwt-decode";

export default {
    state: {
        authInfo: {},
        tokens: {}
    },

    getters: {
        authInfo(state) {
            return state.authInfo;
        },
        tokens(state) {
            return state.tokens;
        }
    },

    mutations: {
        setAuthInfo(state, info = {}) {
            state.authInfo = info;
        },
        setTokens(state, tokens = {}) {
            state.tokens = tokens;
            if (Object.keys(tokens).length) {
                localStorage.setItem("tokens", JSON.stringify(tokens));
            } else {
                localStorage.removeItem("tokens");
            }
        },
        setAccessToken(state, {accessToken}) {
            state.tokens.accessToken = accessToken;
        }
    },

    actions: {
        async login(context, credentials) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/auth/login/",
                    method: "POST",
                    body: credentials
                });
                context.commit("setTokens", responseBody);
                context.commit("setAuthInfo", jwt_decode(responseBody.accessToken));
            } catch (e) {
                context.commit("setAuthError", e);
            }
        },
        logout(context) {
            context.commit("setTokens");
            context.commit("setAuthInfo");
        },
        async checkIfLoggedIn(context) {
            let tokensJson = localStorage.getItem("tokens");
            if (tokensJson) {
                let tokens = JSON.parse(tokensJson);
                context.commit("setTokens", tokens);
                context.commit("setAuthInfo", jwt_decode(tokens.accessToken));
            }
        },
        async checkIfAccessTokenExpired(context){
            //  
            return context.getters.authInfo.exp <= (new Date().getTime() / 1000)
        },
        async updateRefreshToken(context) {
            if (!context.getters.tokens.refreshToken) {
                await context.dispatch("logout");
                return
            }
            let responseBody = await context.dispatch("apiRequest", {
                address: "/api/v1/auth/refresh_token/",
                method: "POST",
                body: context.getters.tokens
            });
            if (responseBody) {
                context.commit("setAccessToken", responseBody);
                context.commit("setAuthInfo", jwt_decode(responseBody.accessToken));
            }
        }
    }
}