<template>
  <fragment>
    <LoadingOverlay v-if="loading"/>

    <LoadingOverlay v-if="sending" :text="$t('  ')"/>

    <MailingForm
        :loading="loading" :accountGroupsList="accountGroupsList"
        :image="image" @changeImage="e => image = e"
        v-model="form" @onSend="send"
    />
  </fragment>
</template>

<script>
import {Fragment} from "vue-fragment";
import LoadingOverlay from "@/components/Loading/LoadingOverlay.vue";
import MailingForm from "@/components/MailingForm.vue";

export default {
  components: {
    LoadingOverlay, MailingForm, Fragment
  },

  data: () => ({
    loading: true,
    sending: false,
    accountGroupsList: undefined,
    image: undefined,
    form: {
      when: {
        now: false,
        date: undefined,
        time: undefined
      },
      filters: {
        all_groups: true,
        groups: [],
        all_sexes: true,
        sex: undefined,
        all_ages: true,
        age_range: {
          from_age: undefined,
          to_age: undefined
        }
      },
      message: {
        text: undefined,
        photo: undefined
      }
    }
  }),

  methods: {
    async send() {
      this.sending = true;
      if (this.image) {
        let form = new FormData();
        form.append("photo", this.image, this.name);
        this.form.message.photo = (await this.$store.dispatch("postMailingSavePhoto", form)).path;
        if (!this.form.message.photo) {
          this.sending = false;
          return;
        }
      }
      let mailing = await this.$store.dispatch("postMailingCreate", this.form);
      if (!mailing) {
        this.sending = false;
        return;
      }
      this.sending = false;
      await this.$router.push("/mailing/history/")
    }
  },

  async mounted() {
    document.title = this.$t("");
    this.accountGroupsList = (await this.$store.dispatch("getProbonusInfo"))?.account_groups;
    this.loading = false;
  }
}
</script>