<template>
  <v-app>
    <component :is="layout"/>
  </v-app>
</template>

<script>
import MainLayout from "@/layouts/MainLayout.vue";
import LoadingLayout from "@/layouts/LoadingLayout.vue";
import UnexpectedErrorLayout from "@/layouts/UnexpectedErrorLayout.vue";
import LoginLayout from "@/layouts/LoginLayout.vue";

export default {
  data: () => ({
    loading: true
  }),

  components: {
    MainLayout, LoadingLayout, UnexpectedErrorLayout, LoginLayout
  },

  computed: {
    layout() {
      if (this.loading) {
        return "LoadingLayout";
      } else if (this.$store.getters.unexpectedError) {
        return "UnexpectedErrorLayout";
      } else if (Object.keys(this.$store.getters.tokens).length) {
        return "MainLayout";
      } else {
        return "LoginLayout";
      }
    }
  },

  async mounted() {
    await this.$store.dispatch("getLocale");
    await this.$store.dispatch("checkIfLoggedIn");
    this.loading = false;
  }
};
</script>
