<template>
  <v-card :width="350" :disabled="loading" elevation="4" outlined style="border-width: 2px">
    <v-card-title>
      <T></T>
    </v-card-title>

    <v-form @submit.prevent="$emit('onLogin')">
      <v-card-text>
        <TextField v-model="model.login" :label="$t('')"/>

        <TextField v-model="model.password" :label="$t('')" type="password"/>

        <template v-if="showHidden">
          <div class="d-flex flex-row">
            <TextField
                v-model="model.probonus_info.host"
                :label="$t('')"
                class="flex-shrink-0 pr-1"
            />

            <TextField
                v-model="model.probonus_info.port"
                :label="$t('')"
            />
          </div>
        </template>

        <div v-if="errorText" style="text-align: center">
            <span class="red--text">
              {{ errorText }}
            </span>
        </div>
      </v-card-text>

      <v-card-actions>
        <v-spacer/>
        <v-btn text type="submit" :loading="loading" color="primary">
          <T></T>
        </v-btn>
      </v-card-actions>
    </v-form>

    <v-snackbar :value="$store.getters.authError.detail" dark>
      <v-icon>mdi-alert-circle-outline</v-icon>
      {{ $store.getters.authError.detail }}
    </v-snackbar>
  </v-card>
</template>

<script>
import TextField from "./TextField.vue";

export default {
  components: {
    TextField
  },

  props: ["value", "loading", "showHidden", "errorText"],

  emits: ["onLogin", "input"],

  computed: {
    model: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("input", v);
      }
    }
  }
}
</script>