export default {
    state: {
        mailings: []
    },

    getters: {
        mailings(state) {
            return state.mailings;
        }
    },

    mutations: {
        setMailings(state, mailings = []) {
            state.mailings = mailings;
        }
    },

    actions: {
        async postMailingSavePhoto(context, image) {
            try {
                return await context.dispatch("apiRequest", {
                    address: "/api/v1/mailing/upload_photo/",
                    method: "POST",
                    body: image,
                    multipart: true
                });
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async postMailingCreate(context, body) {
            try {
                return await context.dispatch("apiRequest", {
                    address: "/api/v1/mailing/",
                    method: "POST",
                    body: body
                });
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getMailings(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/mailing/"
                });
                context.commit("setMailings", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async deleteUnsentMailing(context, id) {
            try {
                return await context.dispatch("apiRequest", {
                    address: `/api/v1/mailing/${id}/`,
                    method: "DELETE"
                })
            } catch (e) {
                context.commit("addError", e);
            }
        }
    }
}