export default {
    state: {
        errors: [],
        authError: {},
        unexpectedError: "",
    },

    getters: {
        errors(state) {
            return state.errors;
        },
        authError(state) {
            return state.authError;
        },
        unexpectedError(state) {
            return state.unexpectedError;
        }
    },

    mutations: {
        addError(state, error) {
            while (state.errors.length >= 5) {
                state.errors.splice(0, 1);
            }
            if (error) {
                state.errors.push(error);
            }
        },
        deleteError(state, index = 0) {
            state.errors.splice(index, 1);
        },
        setAuthError(state, error = {}) {
            state.authError = error;

        },
        setUnexpectedError(state, error = "") {
            state.unexpectedError = error;
        }
    }
}
