<template>
  <v-card>
    <v-card-title>
      <T> #</T>
      {{ review.id }}


      <v-rating
          :value="review.grade"
          color="amber"
          background-color="secondary"
          dense
          half-increments
          readonly
          size="23"
          class="ml-3"
      ></v-rating>
    </v-card-title>

    <v-skeleton-loader v-if="loading" type="list-item-three-line"/>

    <v-container v-else fluid>
      <v-row>
        <v-col cols="6" md="4">
          <T>:</T>
          {{ `${user.guest_info?.lName || ""} ${user.guest_info?.fName || ""} ${user.guest_info?.mName || ""}` }}
        </v-col>

        <v-col cols="6" md="4">
          <T>:</T>
          {{ sex[user.guest_info?.sex] || "-" }}
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="6" md="4">
          <T>Telegram:</T>
          <a v-if="user.username" :href="`https://t.me/${user.username}`">@{{ user.username }}</a>
          <span v-else>-</span>
        </v-col>

        <v-col cols="6" md="4">
          <T>:</T>
          {{ user.guest_info?.phones.filter(phone => phone.isPrimary)[0]?.number || "-" }}
        </v-col>

        <v-col cols="6" md="4">
          <T>Email:</T>
          {{ user.guest_info?.emails.filter(email => email.isPrimary)[0]?.address || "-" }}
        </v-col>
      </v-row>

      <v-divider class="mt-5 mb-5"/>

      <v-card v-for="message in review.messages" :key="message.id" shaped
              max-width="500" color="grey lighten-2" class="mt-2"
      >
        <v-card-text class="text--primary">
          {{ message.text }}
        </v-card-text>

        <v-img v-if="message.photo" :src="message.photo" max-height="350"/>
      </v-card>
    </v-container>

    <v-card-actions>
      <v-spacer/>
      <v-btn v-if="review.status === 0 || review.status === 1" color="primary" @click="$emit('processed')">
        <T></T>
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  props: ["loading", "review", "user"],

  emits: ["processed"],

  computed: {
    sex() {
      return [this.$t(""), this.$t("")]
    }
  }
}
</script>