<template>
  <fragment>
    <LoadingOverlay v-if="loading"/>

    <ReviewsTable
        v-if="$store.getters.config.bot_settings && $store.getters.config.bot_settings.allow_reviews"
        :loading="loading" :items="formattedReviews"
    />

    <v-card v-else class="fill-height">
      <v-row align="center" justify="center">
        <h2 class="text-center">
          <T>       "     "   ""</T>
        </h2>
      </v-row>
    </v-card>
  </fragment>
</template>

<script>
import {Fragment} from "vue-fragment";
import LoadingOverlay from "@/components/Loading/LoadingOverlay.vue";
import ReviewsTable from "@/components/ReviewsTable.vue";

export default {
  components: {
    LoadingOverlay, Fragment, ReviewsTable
  },

  data: () => ({
    loading: true,
  }),

  computed: {
    formattedReviews() {
      return this.$store.getters.reviews.map(
          (review) => {
            review.formattedDatetimeBegin = (new Date(review.datetime_begin)).toLocaleString();
            review.formattedText = review.messages.reduce(
                (accumulator, currentValue) => {
                  return [
                      accumulator, currentValue.text, currentValue.photo ? this.$t("[]") : undefined
                  ].join(" ");
                }, ""
            );
            return review;
          }
      )
    }
  },

  async mounted() {
    document.title = this.$t("");
    await this.$store.dispatch("getConfig")
    await this.$store.dispatch("getReviews");
    this.loading = false;
  }
}
</script>
