<template>
  <v-expansion-panels v-model="model" accordion multiple focusable>
    <slot/>
  </v-expansion-panels>
</template>

<script>
export default {
  props: ["value"],

  computed: {
    model: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit('input', v);
      }
    }
  }
}
</script>