export default {
    state: {
        descriptionAndPolitics: {},
        botPhrases: {},
        botPhrasesMeta: {}
    },

    getters: {
        descriptionAndPolitics(state) {
            return state.descriptionAndPolitics;
        },
        botPhrases(state) {
            return state.botPhrases;
        },
        botPhrasesMeta(state) {
            return state.botPhrasesMeta;
        }
    },

    mutations: {
        setDescriptionAndPolitics(state, value = {}) {
            state.descriptionAndPolitics = value;
        },
        setBotPhrases(state, value = {}) {
            state.botPhrases = value;
        },
        setBotPhrasesMeta(state, value = {}) {
            state.botPhrasesMeta = value;
        }
    },

    actions: {
        async getBotInfoByApiToken(context, token) {
            try {
                return await context.dispatch("apiRequest", {
                    address: `/api/v1/bot/info/${token}/`
                });
            } catch (e) {
                if (e.error_class === "ExternalServiceUnsuccessfulResponseError") {
                    throw e;
                } else {
                    context.commit("addError", e);
                }
            }
        },
        async getDescriptionAndPolitics(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/bot/description-and-politics/"
                });
                context.commit("setDescriptionAndPolitics", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async putDescriptionAndPolitics(context, value) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/bot/description-and-politics/",
                    method: "PUT",
                    body: value
                });
                context.commit("setDescriptionAndPolitics", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getBotPhrases(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/bot/phrases/"
                });
                context.commit("setBotPhrases", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async getBotPhrasesMeta(context) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/bot/phrases/meta/"
                })
                context.commit("setBotPhrasesMeta", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        },
        async putBotPhrases(context, phrases) {
            try {
                let responseBody = await context.dispatch("apiRequest", {
                    address: "/api/v1/bot/phrases/",
                    method: "PUT",
                    body: phrases
                });
                context.commit("setBotPhrases", responseBody);
            } catch (e) {
                context.commit("addError", e);
            }
        }
    }
}