export default {
    actions: {
        async getUtmReport(context, {dateFrom, dateTo}) {
            try {
                return await context.dispatch("apiRequest", {
                    address: `/api/v1/reports/utm/?date_from=${dateFrom}&date_to=${dateTo}`
                })
            } catch (e) {
                context.commit("addError", e);
            }
        }
    }
}