<template>
  <LoginForm :loading="loading" :showHidden="showHidden" :errorText="errorText" @onLogin="login" v-model="form"/>
</template>

<script>
import LoginForm from "@/components/LoginForm"

export default {
  components: {
    LoginForm
  },

  data: () => ({
    loading: true,
    showHidden: false,
    errorText: undefined,
    form: {
      login: "",
      password: "",
      probonus_info: undefined
    }
  }),

  methods: {
    async login() {
      this.loading = true;
      this.errorText = null;
      await this.$store.dispatch("login", this.form);
      if (Object.keys(this.$store.getters.authError).length) {
        if (this.$store.getters.authError.error_class === "ExternalServiceConnectionError") {
          this.form.probonus_info = await this.$store.dispatch("getProbonusConfigAddress");
          this.showHidden = true;
          this.errorText = this.$t(
              "      ProBonus,     ,   ,  ."
          );
        }
        setTimeout(() => this.$store.commit("setAuthError"), 4000);
      }
      this.loading = false;
    }
  },

  async mounted() {
    document.title = this.$t("");
    this.loading = false;
  }
}
</script>